/**
 *  uni.showModal 
 */
function updateUseModal(packageInfo) {
	const {
		title, // 
		contents, // 
		is_mandatory, // 
		url, // 
		platform, // 
		type // 
	} = packageInfo;

	let isWGT = type === 'wgt'
	let isiOS = !isWGT ? platform.includes('iOS') : false;
	let confirmText = isiOS ? '' : ''

	return uni.showModal({
		title,
		content: contents,
		showCancel: !is_mandatory,
		confirmText,
		success: res => {
			if (res.cancel) return;

			// 
			if (isiOS) {
				plus.runtime.openURL(url);
				return;
			}

			uni.showToast({
				title: '',
				duration: 1000
			});

			// wgt  
			downloadTask = uni.downloadFile({
				url,
				success: res => {
					if (res.statusCode !== 200) {
						console.error('', err);
						return;
					}
					// 
					plus.runtime.install(res.tempFilePath, {
						force: false
					}, () => {
						if (is_mandatory) {
							//app
							plus.runtime.restart();
							return;
						}
						uni.showModal({
							title: '',
							success: res => {
								if (res.confirm) {
									//app
									plus.runtime.restart();
								}
							}
						});
					}, err => {
						uni.showModal({
							title: '',
							content: err
								.message,
							showCancel: false
						});
					});
				}
			});
		}
	});
}
